.leaflet-routing-icon-continue {
  background-position: 0 0;
}
.leaflet-routing-icon-sharp-right {
  background-position: -20px 0;
}
.leaflet-routing-icon-turn-right {
  background-position: -40px 0;
}
.leaflet-routing-icon-bear-right {
  background-position: -60px 0;
}
.leaflet-routing-icon-u-turn {
  background-position: -80px 0;
}
.leaflet-routing-icon-sharp-left {
  background-position: -100px 0;
}
.leaflet-routing-icon-turn-left {
  background-position: -120px 0;
}
.leaflet-routing-icon-bear-left {
  background-position: -140px 0;
}
.leaflet-routing-icon-depart {
  background-position: -160px 0;
}
.leaflet-routing-icon-enter-roundabout {
  background-position: -180px 0;
}
.leaflet-routing-icon-arrive {
  background-position: -200px 0;
}
.leaflet-routing-icon-via {
  background-position: -220px 0;
}

.leaflet-routing-geocoders div {
  padding: 4px 0px 4px 0px;
}

.leaflet-routing-geocoders input {
  width: 303px;
  width: calc(100% - 4px);
  line-height: 1.67;
  border: 1px solid #ccc;
}

.leaflet-routing-geocoders button {
  font: bold 18px "Lucida Console", Monaco, monospace;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: white;
  margin: 0;
  margin-right: 3px;
  float: right;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.leaflet-routing-add-waypoint:after {
  content: "+";
}

.leaflet-routing-reverse-waypoints:after {
  font-weight: normal;
  content: "\21c5";
}

.leaflet-routing-geocoders button:hover {
  background-color: #eee;
}

.leaflet-routing-geocoders input,
.leaflet-routing-remove-waypoint,
.leaflet-routing-geocoder {
  position: relative;
}

.leaflet-routing-geocoder-result {
  font: 12px/1.5 "Helvetica Neue", Arial, Helvetica, sans-serif;
  position: absolute;
  max-height: 0;
  overflow: hidden;
  transition: all 0.5s ease;
  z-index: 1000; /* Arbitrary, but try to be above "most" things. */
}

.leaflet-routing-geocoder-result table {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 0 0 4px 4px;
  background-color: white;
  cursor: pointer;
}

.leaflet-routing-geocoder-result-open {
  max-height: 800px;
}

.leaflet-routing-geocoder-selected,
.leaflet-routing-geocoder-result tr:hover {
  background-color: #eee;
}

.leaflet-routing-geocoder-no-results {
  font-style: italic;
  color: #888;
}

.leaflet-routing-remove-waypoint {
  background-color: transparent;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}

.leaflet-routing-remove-waypoint:after {
  position: absolute;
  display: block;
  width: 15px;
  height: 1px;
  z-index: 1;
  right: 1px;
  top: 4px;
  bottom: 0;
  margin: auto;
  padding: 2px;
  font-size: 18px;
  font-weight: bold;
  content: "\00d7";
  text-align: center;
  cursor: pointer;
  color: #ccc;
  background: white;
  padding-bottom: 16px;
  margin-top: -16px;
  padding-right: 4px;
  line-height: 1;
}

.leaflet-routing-remove-waypoint:hover {
  color: black;
}

.leaflet-routing-instruction-distance {
  width: 48px;
}

.leaflet-routing-collapse-btn {
  position: absolute;
  top: 0;
  right: 6px;
  font-size: 24px;
  color: #ccc;
  font-weight: bold;
}

.leaflet-routing-collapse-btn:after {
  content: "\00d7";
}

